

footer{
  background: #141414;
  padding: 90px 0;
  margin-top: 50px;
  @include mobileL{
    padding: 40px 0;
  }
  .footer-title{
    display: flex;
    padding-bottom: 60px;
    align-items: center;
    border-bottom: solid 1px #4D4D4D;;
    @include mobileL{
      padding-bottom: 10px;
      flex-direction: column;
      align-items: normal;
    }
    .footer-title-text{
      margin-left: 15px;
      @include mobileL{
        margin-left: 0;
        margin-bottom: 20px;
        margin-top: 10px ;
      }
    }
  }
}

.footer-title-text{
  font-family: 'Libre Franklin';
  font-style: normal;
  font-weight: 400;
  font-size: 10px;
  line-height: 12px;
  letter-spacing: 1px;
  text-transform: uppercase;
  color: #666666;
}

.footer-categories-item, .footer-link{
  font-family: 'Libre Franklin';
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 24px;
  color: #FFFFFF;
}

.footer-info{
  margin-top: 60px;
  width: 100%;
  display:flex;
  @include tabletM{
    flex-direction: column;
  }
}
.footer-categories{
  .footer-title-text{
    margin-bottom: 20px;
  }
  .footer-categories-list{
    min-width: 900px;
    display: grid;
    grid-template-columns: repeat(4, auto);
    @include mobileL{
      grid-template-columns: repeat(2, auto);
    }
    @include tabletM{
      min-width: auto;
    }
    .footer-categories-item{
      margin-bottom: 20px;
    }
  }
}

.footer-form{
  .footer-title-text{
    margin-bottom: 20px;
  }
  @include tabletM{
    margin: 50px 0;
  }
  @include mobileL{
    margin: 30px 0;
  }
  .footerForm{
    display: flex;
    input{
      border: none;
      outline: none;
      height: 50px;
      width: 225px;
      font-family: 'Libre Franklin';
      font-style: normal;
      font-weight: 400;
      font-size: 10px;
      line-height: 12px;
      letter-spacing: 1px;
      text-transform: uppercase;
      color: #000000;
      padding-left: 20px;
      &::placeholder{
        font-family: 'Libre Franklin';
        font-style: normal;
        font-weight: 400;
        font-size: 10px;
        line-height: 12px;
        letter-spacing: 1px;
        text-transform: uppercase;
        color: #666666;
      }
    }
    button{
      background: black;
      height: 50px;
      width: 80px;
      font-family: 'Libre Franklin';
      font-style: normal;
      font-weight: 400;
      font-size: 10px;
      line-height: 12px;
      letter-spacing: 1px;
      text-transform: uppercase;
      color: #FFFFFF;
      transition:0.35s;
      &:hover{
        background: #9b9b9b;
        color: black;
      }
    }
  }
}

.footer-links{
  display: flex;
  margin-top: 50px;
  @include mobileL{
    flex-direction: column;
  }
  .footer-links-list{
    margin-right: 155px;
    display: flex;
    flex-direction: column;
    @include tablet{
      margin-right: 50px;
    }
    @include mobileL{
      margin-bottom: 20px;
    }
    .footer-title-text{
      margin-bottom: 20px;
    }
    .footer-link{
      margin-bottom: 20px;
    }
    &:last-child{
      margin-right: 0;
    }
  }
}