*{
  box-sizing: border-box;
  font-family: var(--secondaryFont);
  font-weight: normal;
  margin: 0;
  padding: 0;
}
body{
  background: #FFFFFF;
  overflow-x:hidden;
}

.container {
  padding-left: 120px;
  padding-right: 120px;
  max-width: 1920px;
  margin: auto;
  width: 100%;
}
img{
  display: block;
  max-width: 100%;
  height: auto;
}

button{
  cursor: pointer;
}

.overflow{
  overflow: hidden
}

a{
  text-decoration: none;
}

li, il{
  list-style-type: none;
}

a,p,ul,li{
  font-family: var(--mainFont);
  font-weight: bold;

}

button {
  padding: 0;
  border: none;
  font: inherit;
  color: inherit;
  background-color: transparent;
  cursor: pointer;
}

.wrapper{
  width: 100%;
  max-width: 1200px;
 margin: 0 auto;
  @include tabletM{
    padding: 0 30px;
  }
  @include mobileL{
    padding: 0 15px;
  }
}

@import url('https://fonts.googleapis.com/css2?family=Libre+Franklin:wght@400;700&family=Source+Serif+Pro&family=Tenor+Sans&display=swap');

h1{
  font-family: 'Tenor Sans';
  font-style: normal;
  font-weight: 400;
  font-size: 36px;
  line-height: 42px;
  text-transform: uppercase;
  color: #000000;
  @include mobileL{
    font-size: 32px;
    line-height: 38px;
  }
 &.small{
   font-size: 20px;
   line-height: 24px;
 }
}

.category{
  font-family: 'Libre Franklin';
  font-style: normal;
  font-weight: 400;
  font-size: 10px;
  line-height: 12px;
  letter-spacing: 1px;
  text-transform: uppercase;
  color: #999999;
  &.small{
    font-size: 10px;
    line-height: 12px;
  }
}

.news-text{
  font-family: 'Source Serif Pro';
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  color: #666666;
  @include mobileL{
    font-size: 14px;
    line-height: 20px;
  }
}

.author{
  font-family: 'Libre Franklin';
  font-style: normal;
  font-weight: 700;
  font-size: 10px;
  line-height: 12px;
  letter-spacing: 1px;
  text-transform: uppercase;
  color: #000000;
  &.small{
    font-size: 10px;
    line-height: 12px;
  }
}