
.header{
  border-bottom: solid 1px #E6E6E6;;
    position: fixed;
    width: 100%;
    top: 0;
    left: 0;
    z-index: 20;
    background:#FFFFFF;
    height: auto;
  .header__burger{
    display: none;
    @include tablet{
      display: block;
      position: relative;
      width: 30px;
      height: 20px;
    }
    z-index: 5;
  }
  @include tablet{
    .header__burger:before,
    .header__burger:after {
      content: '';
      background: #000000;
      position: absolute;
      width: 100%;
      height: 2px;
      left: 0;
      transition: all 0.3s ease 0s;
    }
    .header:before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: #000000;
      z-index: 3;
    }
    .header__burger:before {
      top: 0;
    }
    .header__burger:after {
      height: 2.4px;
      bottom: 0;
    }
    .header__burger span {
      position: absolute;
      background: #000000;
      left: 0;
      width: 100%;
      height: 2.4px;
      top: 8.4px;
      transition: all 0.3s ease 0s;
    }

    .header__burger.active span {
      transform: scale(0);
    }
    .header__burger.active:before {
      transform: rotate(45deg);
      top: 8.6px;
    }
    .header__burger.active:after {
      transform: rotate(-45deg);
      bottom: 9px;
    }
  }
   &__body{
     display: flex;
     justify-content: space-between;
     padding-top: 35px;
     padding-bottom: 35px;
     @include tabletM{
       padding-top: 25px;
       padding-bottom: 25px;
     }
     .header__nav{
       .header__nav-item{
         font-family: 'Libre Franklin';
         font-style: normal;
         font-weight: 400;
         font-size: 14px;
         line-height: 24px;
         color: #000000;
         margin-right: 40px;
         &:last-child{
           margin-right: 0;
         }
         &:hover{
           color: #363B3E;
           transition: 0.3s;
         }
       }
       @include tablet{
         display: none;
       }
     }
   }
}