

.general-news{
  display: flex;
  margin-top: 150px;
  @include tabletM{
    margin-top: 100px;
  }
  @include mobileL{
    flex-direction: column;
  }
  .general-news-img{
    min-width: 720px;
    margin-right: 50px;
    height: auto;
    @include tabletM{
      min-width: 500px;
    }
    @include tabletL{
      min-width: 30%;
      margin-right: 30px;
    }
    @include mobileL{
      margin-right: 0;
      margin-bottom: 10px;
    }
    img{
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  .general-news-info{
    width: auto;
    h1{
      margin-top: 18px;
      margin-bottom: 27px;
      @include mobileL{
        margin-top: 10px;
        margin-bottom: 20px;
      }
    }

    .author{
      margin-top: 28px;
    }
  }
}


.new-post-list{
  margin-top: 60px;
  display: grid;
  grid-template-columns: repeat(3, auto);
  border-bottom: solid 1px #E6E6E6;
  padding-bottom: 30px;
  @include tabletM{
    grid-template-columns: repeat(2, auto);
    gap: 20px;
  }
  @include mobileL{
    grid-template-columns: repeat(1, auto);
  }
  .new-post{
    max-width: 385px;
    min-height: 480px;
    margin-bottom: 20px;
    transition: 0.35s;
    @include tabletM{
      max-width: none;
      min-height: auto;
    }
    @include mobileL{
      min-height: 400px;
    }
    &:hover{
      transform: scale(1.03);
    }
    .new-post-img{
      width: 100%;
      max-height: 255px;
      @include tabletM{
        max-height: none;
      }
      img{
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }
    .new-post-info{
      margin-top: 25px;
      @include mobileL{
        margin-top: 15px;
      }
      h1{
         margin-top: 18px;
         margin-bottom: 15px;
        @include mobileL{
          margin-top: 10px;
          margin-bottom: 13px;
        }
      }
    }
  }
}

.latest-posts{
  margin-top: 90px;
  border-bottom: solid 1px #E6E6E6;
  @include mobileL{
    margin-top: 30px;
  }
}
.latest-post-list{
  margin-top: 30px;
  display: grid;
  grid-template-columns: repeat(4, auto);
  @include tabletM{
    grid-template-columns: repeat(3, auto);
    gap: 20px;
  }
  @include tablet{
    grid-template-columns: repeat(2, auto);
  }
  @include mobileL{
    grid-template-columns: repeat(1, auto);
  }
  .latest-post{
    max-width: 285px;
    min-height: 390px;
    margin-bottom: 50px;
    transition: 0.35s;

    @include tabletM{
      max-width: none;
      min-width: auto;
    }
    &:hover{
      transform: scale(1.03);
    }
    .latest-post-img{
      width: 100%;
      max-height: 187px;
      @include tabletM{
        max-height: none;
      }
      img{
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }
    .latest-post-info{
      margin-top: 25px;
      @include mobileL{
        margin-top: 15px;
      }
      h1{
        margin-top: 18px;
        margin-bottom: 15px;
        @include mobileL{
          margin-top: 10px;
          margin-bottom: 13px;
        }
      }
    }
  }
}


.showAll-btn{
  background: #FFFFFF;
  border: 1px solid #000000;
  width: 200px;
  height: 54px;
  font-family: 'Libre Franklin';
  font-style: normal;
  font-weight: 400;
  font-size: 10px;
  line-height: 12px;
  letter-spacing: 1px;
  text-transform: uppercase;
  color: #000000;
  align-items: center;
  text-align: center;
  margin: 0 auto 90px;
  display: block;
  transition: 0.35s;
  &:hover{
    background: black;
    color: #FFFFFF;
  }
  @include mobileL{
    margin: 0 auto 30px;
  }
}

.featured-posts{
  margin-top: 60px;
  .featured-post-list{
    display: grid;
    grid-template-columns: repeat(2, auto);
    margin-top: 30px;
    @include tabletM{
      gap: 20px;
    }
    @include mobileL{
      grid-template-columns: repeat(1, auto);
    }
    .featured-post{
      max-width: 580px;
      min-height: 540px;
      margin-bottom: 50px;
      transition: 0.35s;
      @include tabletM{
        min-height: 300px;
      }
      &:hover{
        transform: scale(1.02);
      }
      .featured-post-img{
        width: 100%;
        max-height: 387px;
        min-height: 387px;
        position: relative;
        @include tabletM{
          max-height: none;
          min-height: 250px;
        }
        img{
          position: absolute;
          width: 100%;
          height: 100%;
          object-fit: cover;
        }
      }
      .featured-post-info{
        margin-top: 25px;
        @include mobileL{
          margin-top: 15px;
        }
        h1{
          margin-top: 18px;
          margin-bottom: 15px;
          @include mobileL{
          margin-top: 10px;
          margin-bottom: 13px;
        }
        }
      }
    }
  }
}